.App{
  max-width: 1001px;
  margin: 0 auto;
  display: flex;
  align-items: center;
  width: 100vw;
  height: 100vh;
  /* overflow: hidden; */
}
.buttons-box{
  display: flex;
  flex-direction: column;
  gap: 5px;
}
.buttons-box__container{
  display: flex;
  gap: 5px;
  transition: 0.3s;
}
.button-style{
  transition: 0.3s;
}
.button-style-active{
  transition: 0.3s;
  background-color: green;
}